
.foodDetail {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 46px;
  width: 100%;
  background: #fff;
  overflow: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.foodDetail.fly-enter-active,
.foodDetail.fly-leave-active {
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.foodDetail.fly-enter,
.foodDetail.fly-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.foodDetail .detail-header {
  position: relative;
  top: 0;
  left: 0;
  widht: 100%;
  height: 0;
  padding-top: 100%;
}
.foodDetail .detail-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.foodDetail .detail-header .back {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 10px;
  padding: 10px;
  color: #fff;
  text-shadow: #000 2px 2px 3px;
}
.foodDetail .detail-content {
  position: relative;
  padding: 18px;
}
.foodDetail .detail-content .name {
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
  line-height: 14px;
}
.foodDetail .detail-content .desc {
  margin: 8px 0 18px 0;
  font-size: 10px;
  color: #93999f;
  line-height: 10px;
}
.foodDetail .detail-content .desc .monthSale {
  margin-right: 12px;
}
.foodDetail .detail-content .price .new {
  font-size: 14px;
  font-weight: 700;
  color: #f01414;
  line-height: 24px;
  margin-right: 12px;
}
.foodDetail .detail-content .price .new span {
  font-size: 10px;
  font-weight: normal;
}
.foodDetail .detail-content .price .old {
  font-size: 10px;
  font-weight: 700;
  color: #93999f;
  line-height: 24px;
  text-decoration: line-through;
}
.foodDetail .detail-content .price .old span {
  font-weight: normal;
}
.foodDetail .detail-content .control {
  position: absolute;
  bottom: 18px;
  right: 18px;
}
.foodDetail .detail-content .addCart {
  position: absolute;
  bottom: 18px;
  right: 18px;
  padding: 6px 12px;
  background: #00a0dc;
  height: 24px;
  border-radius: 24px;
  font-size: 10px;
  color: #fff;
  line-height: 24px;
  opacity: 1;
}
.foodDetail .detail-content .addCart.fade-enter-active,
.foodDetail .detail-content .addCart.fade-leave-active {
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
}
.foodDetail .detail-content .addCart.fade-enter,
.foodDetail .detail-content .addCart.fade-leave-active {
  opacity: 0;
}
.foodDetail .detail-desc {
  margin: 18px;
}
.foodDetail .detail-desc .desc {
  font-size: 14px;
  color: #07111b;
  margin-bottom: 6px;
}
.foodDetail .detail-desc .infos {
  font-size: 12px;
  font-weight: 200;
  color: #4d555d;
  line-height: 24px;
  padding: 0 8px;
}
.foodDetail .rating {
  padding-top: 18px;
}
.foodDetail .rating .title {
  font-size: 14px;
  color: #07111b;
  margin: 0 18px;
}
.foodDetail .rating .rating-wrap {
  padding: 0 18px;
}
.foodDetail .rating .rating-wrap .item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.foodDetail .rating .rating-wrap .item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.foodDetail .rating .rating-wrap .item .user {
  position: absolute;
  top: 16px;
  right: 0;
}
.foodDetail .rating .rating-wrap .item .user .account {
  display: inline-block;
  font-size: 10px;
  color: #93999f;
  line-height: 12px;
  margin-right: 6px;
  vertical-align: top;
}
.foodDetail .rating .rating-wrap .item .time {
  font-size: 10px;
  color: #93999f;
  line-height: 12px;
}
.foodDetail .rating .rating-wrap .item .info {
  padding-top: 6px;
}
.foodDetail .rating .rating-wrap .item .info .icon {
  display: inline-block;
  font-size: 12px;
  line-height: 24px;
}
.foodDetail .rating .rating-wrap .item .info.icon-thumb_up {
  color: #00a0dc;
}
.foodDetail .rating .rating-wrap .item .info.icon-thumb_down {
  color: #93999f;
}
.foodDetail .rating .rating-wrap .item .info .text {
  display: inline-block;
  font-size: 12px;
  color: #07111b;
  line-height: 16px;
}
.foodDetail .rating .rating-wrap .no-rating {
  padding: 16px 0;
  font-size: 12px;
  color: #93999f;
}


