  padding: 24px 12px 18px 24px;
}
.header .avatar {
  display: inline-block;
  margin-right: 16px;
}
.header .avatar img {
  border-radius: 2px;
}
.header .seller-info {
  font-size: 16px;
  display: inline-block;
  vertical-align: top;
  margin-top: 2px;
}
.header .seller-info .brand {
  margin-right: 6px;
  display: inline-block;
  vertical-align: top;
  width: 30px;
  height: 18px;
  background-image: url("brand@2x.png");
  background-size: 30px 18px;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .seller-info .brand {
    background-image: url("brand@3x.png");
}
}
