  line-height: 18px;
}
.header .seller-info .distribution {
  font-size: 12px;
  font-weight: 200;
  line-height: 12px;
  margin: 8px 0 10px 0;
}
.header .seller-info .discount {
  font-size: 10px;
  font-weight: 200px;
  line-height: 12px;
}
.header .seller-info .discount .reduce {
  display: inline-block;
