.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.header .background img {
  display: inline-block;
  width: 100%;
  height: 100%;
  -webkit-filter: blur(20px);
          filter: blur(20px);
}
.header .fade-enter-active,
.header .fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.header .fade-enter,
.header .fade-leave-to {
  opacity: 0;
}
.header .detail {
  position: fixed;
  top: 0;
  left: 0;
