}
.header .detail .detailWrap .discount-info .text {
  display: inline-block;
  font-size: 12px;
  font-weight: 200;
  line-height: 12px;
  vertical-align: top;
  margin-top: 3px;
}
.header .detail .detailWrap .discount-info .main {
