  line-height: 24px;
}
.header .detail .detailWrap .discount-info .fade-enter-active,
.header .detail .detailWrap .discount-info .fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.header .detail .detailWrap .discount-info .fade-enter,
