
.ratingSelect .rating-type {
  padding: 18px 0;
  margin: 0 16px;
  position: relative;
  font-size: 0;
}
.ratingSelect .rating-type:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.ratingSelect .rating-type .com {
  display: inline-block;
  font-size: 12px;
  linet-height: 16px;
  color: #4d555d;
  padding: 8px 12px;
  border-radius: 1px;
  margin-right: 8px;
}
.ratingSelect .rating-type .com.active {
  color: #fff;
}
.ratingSelect .rating-type .com.nagative {
  background: rgba(77,85,93,0.2);
}
.ratingSelect .rating-type .com.nagative.active {
  background: #4d555d;
}
.ratingSelect .rating-type .com.positive {
  background: rgba(0,160,220,0.2);
}
.ratingSelect .rating-type .com.positive.active {
  background: #00a0dc;
}
.ratingSelect .rating-type .com .count {
  font-size: 8px;
  margin-left: 2px;
}
.ratingSelect .rating-switch {
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  color: #93999f;
}
.ratingSelect .rating-switch.active .icon-check_circle {
  color: #00c850;
}
.ratingSelect .rating-switch .icon-check_circle {
  display: inline-block;
  font-size: 24px;
  margin-right: 4px;
  vertical-align: middle;
}
.ratingSelect .rating-switch .text {
  font-size: 12px;
}


