
.seller {
  position: absolute;
  top: 174px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}
.seller .seller-head {
  position: relative;
  padding: 18px 18px 0 18px;
}
.seller .seller-head .title {
  font-size: 14px;
  color: #07111b;
  line-height: 14px;
}
.seller .seller-head .desc {
  padding: 8px 0 18px 0;
  position: relative;
}
.seller .seller-head .desc:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.seller .seller-head .desc .star {
  display: inline-block;
  vertical-align: top;
  margin-right: 8px;
}
.seller .seller-head .desc .rateCount {
  margin-right: 12px;
}
.seller .seller-head .desc .rateCount,
.seller .seller-head .desc .sellCount {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #4d555d;
  line-height: 18px;
}
.seller .seller-head .remark {
  padding: 18px 0;
  font-size: 0;
}
.seller .seller-head .remark .item {
  display: inline-block;
  width: 33%;
  text-align: center;
  border-right: 1px solid rgba(7,17,27,0.1);
}
.seller .seller-head .remark .item:last-child {
  border-right: none;
}
.seller .seller-head .remark .item .title {
  font-size: 10px;
  color: #93999f;
  line-height: 10px;
  margin-bottom: 4px;
}
.seller .seller-head .remark .item .doller {
  font-weight: 200;
  color: #07111b;
  line-height: 24px;
}
.seller .seller-head .remark .item .doller .num {
  font-size: 24px;
}
.seller .seller-head .remark .item .doller .text {
  font-size: 10px;
}
.seller .seller-head .favorite {
  position: absolute;
  width: 46px;
  top: 18px;
  right: 13px;
  text-align: center;
}
.seller .seller-head .favorite span {
  display: block;
  font-size: 24px;
  line-height: 24px;
  margin-bottom: 4px;
}
.seller .seller-head .favorite span.active {
  color: #f01414;
}
.seller .seller-head .favorite .text {
  font-size: 10px;
  color: #4d555d;
  line-height: 10px;
}
.seller .bulletin {
  padding: 18px 18px 0 18px;
}
.seller .bulletin .title {
  padding-bottom: 16px;
}
.seller .bulletin .title h1 {
  font-size: 14px;
  line-height: 14px;
  color: #07111b;
  margin-bottom: 8px;
}
.seller .bulletin .title p {
  font-size: 12px;
  font-weight: 200;
  color: #f01414;
  line-height: 24px;
  padding-left: 12px;
}
.seller .bulletin .supports-wrap .item {
  padding: 16px 12px;
  border-top: 1px solid rgba(7,17,27,0.1);
}
.seller .bulletin .supports-wrap .item .icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
  margin-right: 6px;
  vertical-align: top;
}
.seller .bulletin .supports-wrap .item .icon.decrease {
  background-image: url("decrease_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports-wrap .item .icon.decrease {
    background-image: url("decrease_2@3x.png");
}
}
.seller .bulletin .supports-wrap .item .icon.discount {
  background-image: url("discount_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports-wrap .item .icon.discount {
    background-image: url("discount_2@3x.png");
}
}
.seller .bulletin .supports-wrap .item .icon.guarantee {
  background-image: url("guarantee_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports-wrap .item .icon.guarantee {
    background-image: url("guarantee_2@3x.png");
}
}
.seller .bulletin .supports-wrap .item .icon.invoice {
  background-image: url("invoice_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports-wrap .item .icon.invoice {
    background-image: url("invoice_2@3x.png");
}
}
.seller .bulletin .supports-wrap .item .icon.special {
  background-image: url("special_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports-wrap .item .icon.special {
    background-image: url("special_2@3x.png");
}
}
.seller .bulletin .supports-wrap .item .text {
  display: inline-block;
  font-size: 12px;
  font-weight: 200;
  color: #07111b;
  line-height: 16px;
  vertical-align: top;
}
.seller .pic {
  padding: 18px;
}
.seller .pic h1 {
  font-size: 14px;
  line-height: 14px;
  color: #07111b;
  margin-bottom: 12px;
}
.seller .pic .pic-wrap {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
}
.seller .pic .pic-wrap .pic-item {
  font-size: 0;
}
.seller .pic .pic-wrap .pic-item .item {
  display: inline-block;
  margin-right: 6px;
  width: 120px;
  height: 90px;
}
.seller .pic .pic-wrap .pic-item .item:last-child {
  margin-right: 0;
}
.seller .sellerInfo {
  padding: 18px;
}
.seller .sellerInfo h1 {
  font-size: 14px;
  line-height: 14px;
  color: #07111b;
  margin-bottom: 12px;
}
.seller .sellerInfo .item {
  border-top: 1px solid rgba(7,17,27,0.1);
  padding: 16px 12px;
  font-size: 12px;
  font-weight: 200;
  line-height: 16px;
  color: #07111b;
}


